import newsList from "../../components/news-item/news-item.vue"
export default {
	data() {
		return {
			newsList:[]
		}
	},
	methods: {
		async getNews(){
			const res=await this.$myRequest({
				url:'/api/getnewslist'
			})
			// console.log(res);
			this.newsList=res.data.message;
		},
		goDetail(id){
			uni.navigateTo({
				url:"/pages/news-detail/news-detail?id="+id
			})
		}
	},
	components:{
		"news-list":newsList
	},
	onLoad(){
		this.getNews();
	}
}
